import request from '@/utils/RequestApi'
import qs from 'qs';
export function GetCrossingPortal(query) {
  return request({
    url: '/CrossingPortal/GetCrossingPortal',
    method: 'get',
    params: query
  })
}

export function GetActiveCrossingPortal(query) {
  return request({
    url: '/CrossingPortal/GetActiveCrossingPortal',
    method: 'get',
    params: query
  })
}

export function GetById(query) {
  return request({
    url: '/CrossingPortal/GetById',
    method: 'get',
    params: query
  })
}
export function GetCrossingPortalByAny(query) {
  return request({
    url: '/CrossingPortal/GetCrossingPortalByAny',
    method: 'get',
    params: query
  })
}
export function CheckIsExist(query) {
  return request({
    url: '/CrossingPortal/CheckIsExist',
    method: 'get',
    params: query
  })
}
export function GetByListQ(data) {
  return request({
    url: '/CrossingPortal/GetByListQ',
    method: 'post',
    data: qs.stringify(data)
  })
}
export function Create(data) {
  return request({
    url: '/CrossingPortal/Create',
    method: 'post',
    data: qs.stringify(data)
  })
}

export function Edit(data) {
  return request({
    url: '/CrossingPortal/Edit',
    method: 'post',
    data: qs.stringify(data)
  })
}

export function GetCrossingPortalCheque(query) {
    return request({
      url: '/CrossingPortal/GetCrossingPortalCheque',
      method: 'get',
      params: query
    })
  }
