import Visualization from "./Visualization";
import PatientCard from "./Template/PatientCard";

export function PrintReport(Type, Data) {
  let html = ""
  if (Type == "PatientCard")
    html = PatientCard
  try {
    var oIframe = document.getElementById("ifrmPrint");
    var oDoc = oIframe.contentWindow || oIframe.contentDocument;
    if (oDoc.document) oDoc = oDoc.document;
    oDoc.write("<head><title>title</title>");
    oDoc.write('</head><body onload="this.focus(); this.print();">');
    oDoc.write(Visualization(Data, html, "Set") + "</body>");
    oDoc.close();
    setTimeout(() => {
      document.getElementById("ifrmPrint").src = "";
    }, 2000);
  } catch (e) {
    self.print();
  }


}

export function VisualizationReportHtml(Type, Data) {
  return new Promise((resolve) => {
    let html = ""
    if (Type == "PatientCard")
      html = PatientCard
    resolve(Visualization(Data, html, "Set"))
  })
}

