<template>
  <div class="app-container">
    <div class="filter-container">
      <el-row >
        <el-col :span="14">
          <el-input
            v-model="listQuery.Any"
            placeholder="Search By Any Name Or Id Or Mobile No"
            class="filter-item"
            @keyup.enter.native="handleFilter"
          />
        </el-col>
        <el-col :span="4">
          <Sort-Options
            :Value="listQuery.Sort"
            @Set="
              (v) => {
                listQuery.Sort = v;
                handleFilter();
              }
            "
          />
        </el-col>
        <el-col :span="6">
          <el-button
            v-waves
            class="filter-item"
            type="primary"
            icon="el-icon-search"
            @click="handleFilter"
          >
            Search
          </el-button>
        </el-col>
      </el-row>
    </div>
    <el-card class="box-card">
      <el-divider direction="vertical"></el-divider>
      <span>عدد </span>
      <el-divider direction="vertical"></el-divider>
      <span>{{ totals.rows }}</span>
      <el-divider direction="vertical"></el-divider>
    </el-card>

    <el-table
      v-loading="listLoading"
      :data="list"
      border
      fit
      height="500"
      highlight-current-row
      style="width: 100%"
      @sort-change="sortChange"
    >
      <el-table-column label="#" width="80" align="center">
        <template slot-scope="scope">
          <Drawer-Print Type="PatientCard" :Data="scope.row" />
        </template>
      </el-table-column>
      <el-table-column label="الهاتف" prop="mobileNo" align="center"></el-table-column>

      <el-table-column align="center"  label="الاسم">
        <el-table-column prop="patName" ></el-table-column>
        <el-table-column
          prop="patArName"
        ></el-table-column>
      </el-table-column>
      <el-table-column
        label="رقم"
        align="center"
        width="80"
        prop="id"
      >
      </el-table-column>
    </el-table>
    <pagination
      v-show="totals.rows > 0"
      :total="totals.rows"
      :page.sync="listQuery.Page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
    />
  </div>
</template>

<script>
import { GetByListQ } from "@/api/CrossingPortal";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import DrawerPrint from "@/components/PrintRepot/DrawerPrint.vue";
import SortOptions from "@/components/SortOptions";
export default {
  name: "ComplexTable",
  components: {
    Pagination,
    DrawerPrint,
    SortOptions,
  },
  directives: { waves },
  data() {
    return {
      list: [],
      totals: { rows: 0, totals: 0 },
      listLoading: false,
      listQuery: {
        Page: 1,
        Any: "",
        limit: 50,
        Sort: "-id",
        Status: undefined,
      },
      downloadLoading: false,
    };
  },
  created() {
    // this.getList();
  },
  methods: {
    getList() {
      this.listLoading = true;
      //    console.log("sdsad", this.listQuery);
      GetByListQ(this.listQuery).then((response) => {
        this.list = response.items.map((x) => {
          x.qr = x.visitJoQr + ",962" + x.mobileNo + "," + x.patName.replaceAll("'","");
          return x;
        });
        this.totals = response.totals;
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.listQuery.Page = 1;
      this.getList();
    },
    sortChange(data) {
      const { prop, order } = data;
      if (prop === "id") {
        this.sortById(order);
      }
    },
    sortById(order) {
      if (order === "ascending") {
        this.listQuery.sort = "+id";
      } else {
        this.listQuery.sort = "-id";
      }
      this.handleFilter();
    },

    getSortClass: function (key) {
      const sort = this.listQuery.sort;
      return sort === `+${key}` ? "ascending" : "descending";
    },
  },
};
</script>

